a, a:hover {
  text-decoration: none;
}

.masthead {
  a {
    text-decoration: none;
  }
}

.masthead-logo {
  font-size: 1.5rem;
  color: $yellow;
}

.masthead-nav {
  font-size: 1rem;

  a {
    color: #fff;
    display: inline-block;

    &.active {
      color: $yellow;
    }
  }

  a:hover {
    color: $red;
  }
}

.jumbotron {
  font-size: 1.5rem;
  text-align: left;
}

.stroked {
  -webkit-text-stroke: 1px #000;
  color: #fff;
}

legend {
  @extend h2;
  font-weight: bold;
}

table.small-headings {
  th {
    font-size: 0.6rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
h7 {
    sup {
        font-size: 0.5em;
    }
}

.error-message {
  color: $red;
  font-size: 10px;
  font-style: italic;
}

.yellow {
  color: $yellow;
}

footer {
  color: #fff;
  a {
    color: #fff;
  }
}